<template>
  <div class='channel-view fill-flist-darker'>
    <div class='channel-description fill-flist scroll-v'>
      <span class='white-space-preserve' v-html="channel.description"></span>
    </div>
    <div class='channel-messages fill-flist-dark scroll-v'>
      <message class='message' v-for="message in channel.messages" v-bind="{ message }"></message>
    </div>
    <div class='user-count fill-flist-dark'>
      {{ channel.users.length }} characters online
    </div>
    <div class='user-list fill-flist scroll-v'>
      <a href='#' class='user' v-for="user in channel.users" :data-character="user.name">
        <character-name :character="user"></character-name>
      </a>
    </div>
    <div class='chatbox-container fill-flist'>
      <chatbox class='chatbox' :placeholder="`Chatting as ${state.currentCharacter}...`" @submit='handleChatboxSubmit'></chatbox>
    </div>
  </div>
</template>

<script>
import {state, sendChannelMessage} from '../lib/store'
import {Channel} from '../lib/f-chat'

export default {
  props: {
    channel: Channel,
  },
  data () {
    return {state}
  },
  methods: {
    handleChatboxSubmit (message) {
      sendChannelMessage(this.channel.id, message)
    },
  },
  components: {
    Chatbox: require('./Chatbox.vue'),
    Message: require('./Message.vue'),
    CharacterName: require('./CharacterName.vue'),
  },
}
</script>

<style scoped>
.channel-view {
  display: grid;
  grid-template-rows: 5em 1.6em auto 5em;
  grid-template-columns: auto 12em;
  grid-gap: 2px;
}

.channel-description {
  grid-column: span 2;
  padding: 0.5em;
}

.channel-messages {
  grid-row: span 2;
}

.chatbox-container {
  grid-column: span 2;
}

.chatbox {
  display: block;
  width: 100%;
  height: 100%;
}

.user, .user-count {
  display: block;
  padding: 0.2em 0.6em;
}
</style>



