<template>
  <div class='shade fullscreen flex-center' @click.self="$emit('close')">
    <div class='panel fill-flist'>
      <div class='info text-center'>
        <character-name v-bind="{ character }">
          <h1>{{ character.name }}</h1>
        </character-name>
        <p>
          <img style='height: 100px' :src="character.avatar" />
        </p>
        <p v-if="!isSelf" style='font-style: italic; font-size: 0.85em; max-width: 12em'>
          {{ capitalize(character.status) }}
          <span v-if="character.statusMessage !== ''" v-html="' - ' + character.statusMessage"></span>
        </p>
        <p v-else>
          <form @submit.prevent='handleStatusFormSubmit'>
            <fieldset>
              <select v-model="status">
                <option value='online'>Online</option>
                <option value='looking'>Looking</option>
                <option value='busy'>Busy</option>
                <option value='away'>Away</option>
                <option value='dnd'>DND</option>
              </select>
            </fieldset>
            <fieldset style='max-width: 100%'>
              <textarea class='text-input text-center' style='max-width: 100%' placeholder="What's up?" rows='3' v-model="statusMessage"></textarea>
            </fieldset>
            <fieldset>
              <button class='button' type='submit'>Update Status</button>
            </fieldset>
          </form>
        </p>
      </div>
      <div class='actions fill-flist-dark'>
        <a class='action' :href="character.profile" target="_blank"><i class='material-icons icon-small'>link</i> View Profile</a>
        <a v-if="!isSelf" class='action' href='#' @click.prevent="openPrivateChat"><i class='material-icons icon-small'>message</i> Send Message</a>
      </div>
    </div>
  </div>
</template>

<script>
import {Character} from '../lib/f-chat'
import {state, openPrivateChat, updateStatus} from '../lib/store'

export default {
  props: {
    character: Character,
  },
  components: {
    CharacterName: require('./CharacterName.vue'),
  },
  data () {
    return {
      status: '',
      statusMessage: '',
    }
  },
  mounted () {
    if (this.isSelf) {
      this.status = this.character.status
      this.statusMessage = this.character.statusMessage
    }
  },
  methods: {
    capitalize (text) {
      return text[0].toUpperCase() + text.slice(1).toLowerCase()
    },
    openPrivateChat () {
      openPrivateChat(this.character.name)
      this.$emit('close')
    },
    handleStatusFormSubmit () {
      updateStatus(this.status, this.statusMessage)
    },
  },
  computed: {
    isSelf () {
      return this.character.name === state.currentCharacter
    },
  },
}
</script>

<style lang="scss" scoped>
.shade {
  background-color: rgba(black, 0.75);
}

.panel {
  width: max-content;
  box-shadow: 0 4px 8px rgba(black, 0.7);
}

.info {
  padding: 0em 1em;
}

.action {
  display: block;
  padding: 0.3em 0.7em;
  transition: 0.2s background-color;
  line-height: 1.5;

  &:hover {
    background-color: rgba(black, 0.2);
  }
}
</style>



