<template>
  <div class='view fill-flist-darker'>
    <div class='info fill-flist scroll-v'>
      TODO: user name + status
    </div>
    <div class='channel-messages fill-flist-dark scroll-v'>
      <message class='message' v-for="message in privateChat.messages" v-bind="{ message }"></message>
    </div>
    <div class='chatbox-container fill-flist'>
      <chatbox class='chatbox' :placeholder="`Chatting as ${state.currentCharacter}...`" @submit='handleChatboxSubmit'></chatbox>
    </div>
  </div>
</template>

<script>
import {state, sendPrivateMessage} from '../lib/store'
import {PrivateChat} from '../lib/f-chat'

export default {
  props: {
    privateChat: PrivateChat,
  },
  data () {
    return {state}
  },
  methods: {
    handleChatboxSubmit (message) {
      sendPrivateMessage(this.privateChat.partner.name, message)
    },
  },
  components: {
    Chatbox: require('./Chatbox.vue'),
    Message: require('./Message.vue'),
  },
}
</script>

<style scoped>
.view {
  display: grid;
  grid-template-rows: max-content auto 5em;
  grid-template-columns: auto;
  grid-gap: 2px;
}

.info {
  padding: 0.5em;
}

.chatbox {
  display: block;
  width: 100%;
  height: 100%;
}
</style>



