<template>
  <form @submit.prevent="handleSubmit">
    <h1>Choose a Character</h1>
    <p>
      <img :src="getAvatarURL(selected)" />
    </p>
    <fieldset>
      <select v-model="selected">
        <option v-for="name of characters">{{ name }}</option>
      </select>
    </fieldset>
    <fieldset>
      <input type="submit" value="Confirm" />
    </fieldset>
  </form>
</template>

<script>
import {getAvatarURL} from '../lib/f-list'
import * as forage from 'localforage'

export default {
  props: {
    characters: Array,
  },
  data () {
    return {
      selected: '',
    }
  },
  async mounted () {
    this.selected =
      (await forage.getItem('lastCharacter')) || this.characters[0] || ''
  },
  methods: {
    handleSubmit () {
      this.$emit('submit', this.selected)
    },
    getAvatarURL,
  },
  watch: {
    selected (value) {
      forage.setItem('lastCharacter', value)
    },
  },
}
</script>

<style lang="scss" scoped>
</style>



