<template>
  <a href='#' class='action'>
    <i class='material-icons icon-large'>{{ icon }}</i>
  </a>
</template>

<script>
export default {
  props: {
    icon: {type: String, required: true},
  },
}
</script>

<style scoped>
.action {
  display: block;
  opacity: 0.5;
  transition: 0.2s opacity;
}

.action:hover {
  opacity: 1;
}
</style>



