<template>
  <div :class="['message', message.text.startsWith('/me') && 'message--action', 'message--' + message.type]">
    <a href='#' :data-character="message.sender.name">
      <character-name class='sender' :character="message.sender"></character-name>
    </a>
    <span v-html="message.text.replace(/^\/me\s*/, '')" class='white-space-preserve'></span>
  </div>
</template>

<script>
import {Message} from '../lib/f-chat'

export default {
  props: {
    message: Message,
  },
  components: {
    CharacterName: require('./CharacterName.vue'),
  },
}
</script>

<style lang="scss" scoped>
.message {
  padding: 0.2em 0.6em;
}

.message--action {
  font-style: italic;
}

.message--lfrp {
  background-color: rgba(green, 0.2);
}

.sender {
  margin-right: 0.3em;
}
</style>



