export async function getTicket(account, password) {
  const res = await fetch(
    `https://www.f-list.net/json/getApiTicket.php?account=${account}&password=${password}`,
  )
  const data = await res.json()
  if (data.ticket) return data.ticket
  throw data.error
}

export async function getCharacters(account, ticket) {
  const res = await fetch(
    `https://www.f-list.net/json/api/character-list.php?account=${account}&ticket=${ticket}`,
  )
  const data = await res.json()
  if (data.characters) return data.characters
  throw data.error
}

export function getProfileURL(name) {
  return 'https://www.f-list.net/c/' + encodeURI(name.toLowerCase())
}

export function getAvatarURL(name) {
  return `https://static.f-list.net/images/avatar/${encodeURI(name.toLowerCase())}.png`
}



