<template lang="html">
  <div class='chat fill-flist-darker' @pointerdown="handleClick">
    <div class='actions flex-column flex-justify-space-between fill-flist-darker'>
      <div>
        <Action class='action' icon='apps' @pointerdown.native="openChannelList" />
        <Action class='action' icon='people' @pointerdown.native="characterBrowser = true" />
        <Action class='action' icon='account_circle' @pointerdown.native="openSelfCharacterMenu" />
        <Action class='action' icon='settings' />
      </div>
      <div>
        <Action class='action' icon='exit_to_app' />
      </div>
    </div>
    <div class='tabs flex-column fill-flist-dark'>
      <a class='tab fill-flist' href='#' v-for='(tab, i) of tabs' :class="tabIndex === i && 'tab--active'" @click="tabIndex = i">
        <i v-if="tab.view === 'channel-view'" class='material-icons' style='font-size: 1.25em'>public</i>
        <img v-if="tab.view === 'private-chat-view'" :src="tab.props.privateChat.partner.avatar" class='tab-avatar' />
        <span v-html="tab.title"></span>
      </a>
    </div>
    <component v-if="currentTab" :is="currentTab.view" v-bind="currentTab.props"></component>
    <channel-list v-if="channelList" @close="channelList = false"></channel-list>
    <character-browser v-if="characterBrowser" @close="characterBrowser = false"></character-browser>
    <character-menu v-if="characterMenu" v-bind="characterMenu" @close="characterMenu = null"></character-menu>
  </div>
</template>

<script>
import {state, fetchChannelList} from '../lib/store'

export default {
  components: {
    Action: require('./ChatAction.vue'),
    ChannelView: require('./ChannelView.vue'),
    PrivateChatView: require('./PrivateChatView.vue'),
    ChannelList: require('./ChannelList.vue'),
    CharacterBrowser: require('./CharacterBrowser.vue'),
    CharacterMenu: require('./CharacterMenu.vue'),
  },
  data () {
    return {
      state,
      tabIndex: 0,
      channelList: false,
      characterBrowser: false,
      characterMenu: null,
    }
  },
  computed: {
    tabs () {
      const channels = Object.values(state.joinedChannels).map(channel => {
        return {title: channel.title, view: 'channel-view', props: {channel}}
      })

      const privateChats = Object.values(
        state.privateChats
      ).map(privateChat => {
        return {
          title: privateChat.partner.name,
          view: 'private-chat-view',
          props: {privateChat},
        }
      })

      return channels.concat(privateChats)
    },
    currentTab () {
      return this.tabs[this.tabIndex]
    },
  },
  methods: {
    openChannelList () {
      this.channelList = true
      fetchChannelList()
    },
    handleClick (event) {
      for (const el of event.path) {
        if (el.dataset && el.dataset.character) {
          const character = state.onlineCharacters[el.dataset.character]
          if (character) {
            this.characterMenu = {character}
            event.preventDefault()
          }
        }
      }
    },
    openSelfCharacterMenu () {
      this.characterMenu = {
        character: state.onlineCharacters[state.currentCharacter],
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.chat {
  display: grid;
  grid-template-columns: max-content 12em auto;
  grid-template-rows: 100vh;
  grid-gap: 2px;
}

.actions {
  padding: 0.5em;
}

.action + .action {
  margin-top: 1em;
}

.tab {
  padding: 0.5em;
  opacity: 0.4;
  transition: 0.2s opacity;
}

.tab:hover:not(.tab--active) {
  opacity: 0.7;
}

.tab--active {
  opacity: 1;
}

.tab:focus {
  outline: none;
}

.tab-avatar {
  width: 1.25em;
  vertical-align: text-bottom;
}
</style>



