<template>
  <div class='shade fullscreen flex-center' @pointerdown.self="$emit('close')">
    <div class='panel fill-flist'>
      <div class='channel-list fill-flist-dark scroll-v'>
        <a href='#' v-for="ch in channels" class="channel flex-row" :class="isJoined(ch.id) && 'channel--joined'" @click.prevent="handleChannelClicked(ch.id)">
          <span class="flex-grow channel-title" v-html="ch.title"></span>
          <span class="channel-user-count">{{ ch.userCount }}</span>
        </a>
      </div>
      <div class='footer text-center'>
        <input type='text' placeholder="Search..." v-model='searchText' />
        <button class='button' @click="$emit('close')">Done</button>
      </div>
    </div>
  </div>
</template>

<script>
import {state, joinChannel, leaveChannel} from '../lib/store'

function compareNames (a, b) {
  return a.title.localeCompare(b.title)
}

export default {
  data () {
    return {
      state,
      searchText: '',
    }
  },
  computed: {
    channels () {
      const publicChannels = Object.values(state.publicChannelList).sort(
        compareNames
      )
      const privateChannels = Object.values(state.privateChannelList).sort(
        compareNames
      )
      return publicChannels
        .concat(privateChannels)
        .filter(ch =>
          ch.title.toLowerCase().includes(this.searchText.toLowerCase())
        )
    },
  },
  methods: {
    isJoined (channel) {
      return state.joinedChannels[channel] != null
    },
    handleChannelClicked (id) {
      if (this.isJoined(id)) {
        leaveChannel(id)
      } else {
        joinChannel(id)
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.shade {
  background-color: rgba(black, 0.75);
}

.panel {
  width: max-content;
  box-shadow: 0 4px 8px rgba(black, 0.7);
}

.channel-list {
  width: 24em;
  height: 28em;
}

.channel-title, .channel-user-count {
  padding: 0.3em 0.6em;
}

.channel--joined {
  background-color: lighten(rgba(green, 0.15), 20%);
}

.footer {
  padding: 0.5em;
}
</style>



