import * as flist from './f-list'

export function Character (
  name,
  gender = 'none',
  status = 'online',
  statusMessage = ''
) {
  this.name = name
  this.gender = gender
  this.status = status
  this.statusMessage = statusMessage
  this.avatar = flist.getAvatarURL(name)
  this.profile = flist.getProfileURL(name)
}

export function Message (sender, text, type = 'normal') {
  this.sender = sender
  this.text = text
  this.type = type
  this.time = new Date()
}

export function Channel (id) {
  this.id = this.title = id
  this.description = ''
  this.mode = 'both'
  this.messages = []
  this.users = []
  this.ops = []
}

export function PrivateChat (partner) {
  this.partner = partner
  this.messages = []
}

export function ChannelInfo (id, title, userCount, mode) {
  this.id = id
  this.title = title
  this.userCount = userCount
  this.mode = mode
}



