import Vue from 'vue'
import App from './components/App.vue'
import * as store from './lib/store'

import 'normalize.css/normalize.css'
import './styles/index.scss'

// eslint-disable-next-line no-new
new Vue({
  el: '#app',
  render: h => h(App),
  mounted () {
    store.init()
  },
})



