<template>
  <textarea class='chatbox-textarea' v-model='message' :placeholder="placeholder" @keydown.enter="onKeydown"></textarea>
</template>

<script>
export default {
  props: {
    placeholder: String,
  },
  data () {
    return {message: ''}
  },
  methods: {
    onKeydown (event) {
      if (!event.shiftKey && !event.ctrlKey) {
        event.preventDefault()
        const message = this.message.trim()
        if (message.length > 0) {
          this.$emit('submit', message)
        }
        this.message = ''
      }
    },
  },
}
</script>



