<template>
  <div class='shade fullscreen flex-column'>
    <div class='text-center' @click.self="$emit('close')">
      <h1>{{ onlineUserCount }} characters online</h1>
    </div>
    <div class='flex-grow flex-row flex-justify-center flex-wrap scroll-v' @click.self="$emit('close')" style="align-items: flex-start">
      <div class='character fill-flist flex-row' v-for='char in displayedCharacters'>
        <a href='#' :data-character='char.name'>
          <img class='character-avatar' style='width: 100px; height: 100px; display: block' :src='char.avatar' />
        </a>
        <div class='character-info flex-column'>
          <div class='character-name-container fill-flist'>
            <a href='#' :data-character='char.name'>
              <character-name :character="char"></character-name>
            </a>
          </div>
          <div class='character-status fill-flist-dark flex-grow scroll-v'>
            <span v-html='char.statusMessage'></span>
          </div>
        </div>
      </div>
    </div>
    <div class='footer text-center'>
      <input type='text' placeholder="Search all characters..." @input='updateSearchText' />
      <button class='button' @click.self="$emit('close')">Done</button>
    </div>
  </div>
</template>

<script>
import {state} from '../lib/store'

function sortWeight (char) {
  switch (true) {
    case state.friends[char.name]:
      return 0
    case char.status === 'looking':
      return 1
    case char.status === 'online':
      return 2
    default:
      return 3
  }
}

function compareCharacters (a, b) {
  const weight1 = sortWeight(a)
  const weight2 = sortWeight(b)
  if (weight1 !== weight2) {
    return weight1 < weight2
  } else {
    return a.name.localeCompare(b.name)
  }
}

export default {
  data () {
    return {
      searchText: '',
      debounceTimer: null,
      characters: [],
    }
  },
  mounted () {
    this.characters = Object.values(state.onlineCharacters)
  },
  computed: {
    displayedCharacters () {
      const search = this.searchText.toLowerCase()
      return this.characters
        .filter(c => c.name.toLowerCase().includes(search))
        .slice(0, 500)
        .sort(compareCharacters)
    },
    onlineUserCount () {
      return this.characters.length
    },
  },
  methods: {
    updateSearchText (event) {
      if (this.debounceTimer) {
        window.clearTimeout(this.debounceTimer)
      }
      this.debounceTimer = window.setTimeout(
        () => (this.searchText = event.target.value),
        1000
      )
    },
  },
  components: {
    CharacterName: require('./CharacterName.vue'),
  },
}
</script>

<style lang="scss" scoped>
.shade {
  background-color: rgba(black, 0.75);
}

.character {
  margin: 0.5em;
}

.character-info {
  width: 12em;
  height: 100px;
}

.character-name-container, .character-status {
  padding: 0.3em 0.6em;
}

.footer {
  padding: 0.5em;
}
</style>



