import * as flist from '../lib/f-list'
import * as fchat from '../lib/f-chat'
import * as forage from 'localforage'
import meta from '../../package.json'
import Vue from 'vue'

const state = {
  view: 'init',
  account: '',
  ticket: '',
  characters: [],
  currentCharacter: '',
  friends: {},
  admins: {},
  ignored: {},
  onlineCharacters: {},
  publicChannelList: {},
  privateChannelList: {},
  joinedChannels: {},
  privateChats: {},
  socket: null,
}

async function init () {
  if (state.socket) {
    state.socket.close()
  }

  try {
    const {account, ticket} = (await forage.getItem('auth')) || {}
    if (account && ticket) {
      const characters = await flist.getCharacters(account, ticket)
      state.account = account
      state.ticket = ticket
      state.characters = characters
      setView('characterSelect')
    }
  } catch (err) {
    console.warn(err)
    setView('login')
  }
}

function setView (view) {
  state.view = view
}

async function login (account, password) {
  const ticket = await flist.getTicket(account, password)
  const characters = await flist.getCharacters(account, ticket)

  state.account = account
  state.ticket = ticket
  state.characters = characters

  forage.setItem('auth', {account, ticket})
}

function setCharacter (character) {
  state.currentCharacter = character
}

function connectToChatServer () {
  const socket = (state.socket = new WebSocket('wss://chat.f-list.net:9799'))

  socket.onopen = () => {
    const params = JSON.stringify({
      cname: meta.name,
      cversion: meta.version,
      account: state.account,
      ticket: state.ticket,
      character: state.currentCharacter,
      method: 'ticket',
    })

    socket.send('IDN ' + params)
  }

  socket.onmessage = msg => {
    handleSocketMessage(msg.data, socket)
  }

  socket.onclose = () => {}
  socket.onerror = () => {}
}

function sendSocketCommand (cmd, params) {
  if (params) {
    state.socket.send(cmd + ' ' + JSON.stringify(params))
  } else {
    state.socket.send(cmd)
  }
}

function fetchChannelList () {
  sendSocketCommand('CHA')
  sendSocketCommand('ORS')
}

function joinChannel (id) {
  sendSocketCommand('JCH', {channel: id})
}

function leaveChannel (id) {
  sendSocketCommand('LCH', {channel: id})
}

function openPrivateChat (name) {
  Vue.set(
    state.privateChats,
    name,
    new fchat.PrivateChat(state.onlineCharacters[name])
  )
}

function sendChannelMessage (id, text) {
  sendSocketCommand('MSG', {channel: id, message: text})

  const channel = state.joinedChannels[id]
  const char = state.onlineCharacters[state.currentCharacter]
  channel.messages.push(new fchat.Message(char, text, 'self'))
}

function sendPrivateMessage (recipientName, text) {
  sendSocketCommand('PRI', {recipient: recipientName, message: text})

  const chat = state.privateChats[recipientName]
  const char = state.onlineCharacters[state.currentCharacter]
  chat.messages.push(new fchat.Message(char, text, 'self'))
}

function updateStatus (status, statusmsg) {
  const char = state.onlineCharacters[state.currentCharacter]
  char.status = status
  char.statusMessage = statusmsg
  sendSocketCommand('STA', {status, statusmsg})
}

function handleSocketMessage (message, socket) {
  const cmd = message.substring(0, 3)
  const params = message.length > 3 ? JSON.parse(message.substring(4)) : {}

  const handlers = {
    IDN () {
      state.view = 'chat'
      joinChannel('Fantasy')
      joinChannel('Frontpage')
      joinChannel('Story Driven LFRP')
      joinChannel('RP Bar')
      joinChannel('RP Dark City')
    },
    PIN () {
      socket.send('PIN')
    },
    ERR () {
      console.error('Server error:', params.message)
    },
    HLO () {
      console.info(params.message)
    },
    CON () {
      console.info(`There are ${params.count} characters online.`)
    },
    FRL () {
      params.characters.forEach(name => {
        Vue.set(state.friends, name, true)
      })
    },
    ADL () {
      params.ops.forEach(name => {
        Vue.set(state.admins, name, true)
      })
    },
    IGN () {
      if (params.action === 'init') {
        params.characters.forEach(name => {
          Vue.set(state.ignored, name, true)
        })
      }
    },
    LIS () {
      for (const [name, gender, status, statusmsg] of params.characters) {
        Vue.set(
          state.onlineCharacters,
          name,
          new fchat.Character(name, gender, status, statusmsg)
        )
      }
    },
    NLN () {
      Vue.set(
        state.onlineCharacters,
        params.identity,
        new fchat.Character(params.identity, params.gender)
      )
    },
    FLN () {
      Vue.delete(state.onlineCharacters, params.character)
      for (const channel of Object.values(state.joinedChannels)) {
        channel.users = channel.users.filter(name => name !== params.character)
      }
    },
    STA () {
      const char = state.onlineCharacters[params.character]
      char.status = params.status
      char.statusMessage = params.statusmsg
    },
    JCH () {
      const name = params.character.identity
      if (name === state.currentCharacter) {
        Vue.set(
          state.joinedChannels,
          params.channel,
          new fchat.Channel(params.channel)
        )
      }

      const channel = state.joinedChannels[params.channel]
      channel.users.push(state.onlineCharacters[name])
      channel.title = params.title
    },
    LCH () {
      if (params.character === state.currentCharacter) {
        Vue.delete(state.joinedChannels, params.channel)
      } else {
        const channel = state.joinedChannels[params.channel]
        channel.users = channel.users.filter(name => name !== params.character)
      }
    },
    COL () {
      const channel = state.joinedChannels[params.channel]
      channel.ops = params.oplist
    },
    ICH () {
      const channel = state.joinedChannels[params.channel]
      channel.mode = params.mode
      channel.users = params.users.map(
        user => state.onlineCharacters[user.identity]
      )
    },
    CDS () {
      const channel = state.joinedChannels[params.channel]
      channel.description = params.description
    },
    MSG () {
      const channel = state.joinedChannels[params.channel]
      channel.messages.push(
        new fchat.Message(
          state.onlineCharacters[params.character],
          params.message,
          'normal'
        )
      )
    },
    LRP () {
      const channel = state.joinedChannels[params.channel]
      channel.messages.push(
        new fchat.Message(
          state.onlineCharacters[params.character],
          params.message,
          'lfrp'
        )
      )
    },
    PRI () {
      const char = state.onlineCharacters[params.character]
      const chat =
        state.privateChats[params.character] ||
        Vue.set(
          state.privateChats,
          params.character,
          new fchat.PrivateChat(char)
        )
      chat.messages.push(new fchat.Message(char, params.message, 'normal'))
    },
    CHA () {
      for (const info of params.channels) {
        Vue.set(
          state.publicChannelList,
          info.name,
          new fchat.ChannelInfo(
            info.name,
            info.name,
            info.characters,
            info.mode
          )
        )
      }
    },
    ORS () {
      for (const info of params.channels) {
        Vue.set(
          state.privateChannelList,
          info.name,
          new fchat.ChannelInfo(
            info.name,
            info.title,
            info.characters,
            info.mode
          )
        )
      }
    },
  }

  if (handlers[cmd]) {
    handlers[cmd].call(state)
  } else {
    console.warn('Unimplemented command ', cmd, params)
  }
}

export {
  state,
  init,
  setView,
  login,
  setCharacter,
  connectToChatServer,
  fetchChannelList,
  joinChannel,
  leaveChannel,
  openPrivateChat,
  sendChannelMessage,
  sendPrivateMessage,
  updateStatus,
}



