<template>
  <div>
    <div v-if="state.view === 'login'" class="fullscreen flex-column flex-center text-center">
      <Login @submit="handleLoginSubmit" :status="loginStatus"></Login>
    </div>

    <div v-if="state.view === 'characterSelect'" class="fullscreen flex-center text-center">
      <CharacterList
        :characters="state.characters"
        @submit="handleCharacterSubmit">
      </CharacterList>
    </div>

    <Chat v-if="state.view === 'chat'" class="fullscreen"></Chat>
  </div>
</template>

<script>
import CharacterList from './CharacterList.vue'
import Chat from './Chat.vue'
import Login from './Login.vue'
import * as store from '../lib/store'

export default {
  components: {
    CharacterList,
    Chat,
    Login,
  },
  data () {
    return {
      state: store.state,
      loginStatus: '',
    }
  },
  methods: {
    async handleLoginSubmit (account, password) {
      store.setView('')
      this.loginStatus = ''

      try {
        await store.login(account, password)
        store.setView('characterSelect')
      } catch (err) {
        this.loginStatus = err.toString()
        store.setView('login')
      }
    },
    handleCharacterSubmit (character) {
      store.setCharacter(character)
      store.setView('')
      store.connectToChatServer()
    },
  },
}
</script>



